.App {
    width: 100vw;
    min-height: 100vh;
    min-width: 360px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    position: relative;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5) 100%), url('./assets/tile.jpg');
}

.container {
    width: 100%;
    max-width: 1200px;
}


.nav-arrow {
    display: block;
    position: absolute;
    left: 10px;
    top: 20px;
}

.circle {
    position: relative;
    width: 50px;
    height: 50px;
    background-color: transparent;
    border-radius: 50%;
    border: 2px solid #ccc;
    transition: all .3s;
    margin-left: 15px;
}

.arrow {
    position: absolute;
    left: 17px;
    top: 15px;
    width: 16px;
    height: 16px;
    background-color: transparent;
    transform: rotate(45deg);
    border-bottom: 2px solid #ccc;
    border-left: 2px solid #ccc;
    transition: all .3s;

}

.circle:hover,
.circle:hover .arrow {
    border-color: white;
}

.nav-link {
    display: block;
    position: absolute;
    right: 20px;
    top: 35px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    text-shadow: 0px 1px 0 #000000;
    text-decoration: none;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    color: #ccc;
    transition: all .3s;
}

.nav-link:hover {
    color: #fff;
    text-decoration: underline;
}

